<template>
<div class="container-fluid">
  <div class="row">
    <div class="col-sm-12" style="height:30px;"></div>
  </div>
  <div class="row">
    <div class="col-sm-4">

    </div>
    <div class="col-sm-4">
      <div class="card card-block form-section">
        <h2><center>登 录</center></h2>

        <form @submit="login" action="/sign_in" accept-charset="UTF-8" method="post">
          <fieldset class="form-group">
            <label for="merchant_email">邮箱</label>
            <input autofocus="autofocus" class="form-control" type="email" value="" name="email" v-model="email"  />
          </fieldset>

          <fieldset class="form-group">
            <label for="merchant_password">密码</label>
            <input autocomplete="off" class="form-control" type="password" name="password" v-model="password" />
          </fieldset>

          <input type="submit" name="commit" value="登 录" class="btn btn-primary btn-lg btn-block" />

        </form>

      </div>
    </div>
    <div class="col-sm-4">

    </div>
  </div>

</div>

</template>

<script>
  export default {
    data () {
      return {
        email: '',
        password: ''
      }
    },
    methods: {
      login (e) {
        e.preventDefault()
        var data = {
          email: this.email,
          password: this.password
        }
        this.$http.post('http://jiancan.me/api/m1/merchants/login.json', data).then(function (response) {
          localStorage.token = response.data.access_token
          localStorage.email = response.data.email
          this.$dispatch('authenticated')
        }, function (response) {
          this.$dispatch('response-msg', response)
        })
      }
    }
  }
</script>



